<template>
  <header class="header">
    <nav class="nav">
      <div class="container header__menu">
        <a href="" class="logo">
          <img src="@/assets/img/logo.png" alt="" />
        </a>
        <ul
          class="header__list"
          :class="{ _active: !grid }"
          @click="grid = !grid"
        >
          <li
            class="menu-header__li"
            v-for="(names, index) in names"
            :key="index"
          >
            <a class="header__list-link" href="">{{ names }}</a>
          </li>
        </ul>
        <button
          type="button"
          class="menu-header__icon icon-menu"
          :class="{ _active: !grid }"
          @click="grid = !grid"
        >
          <span></span>
        </button>
      </div>
    </nav>
  </header>
</template>
<script>
export default {
  props: {
    names: {
      typeof: Array,
    },
    title: {
      typeof: String,
    },
  },
  data() {
    return {
      grid: true,
    };
  },
};
</script>

<style>
.logo {
  position: relative;
  z-index: 60;
}
.menu-header__icon {
  background: transparent;
  border: none;
  display: none;
}
.icon-menu {
  position: relative;
  z-index: 60;
  width: 30px;
  height: 20px;
}
.icon-menu span {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  transform: translate(0, -50%);
  background-color: #fff;
  transition: 0.3s;
}

.icon-menu::before,
.icon-menu::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: 0.3s;
}

.icon-menu:before {
  top: 0;
}
.icon-menu:after {
  bottom: 0;
}
.icon-menu._active span {
  transform: scale(0);
}
.icon-menu._active:before {
  top: 9px;
  transform: rotate(-45deg);
}
.icon-menu._active:after {
  bottom: 9px;
  transform: rotate(45deg);
}
.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__list {
  display: flex;
  align-items: center;
  gap: 56px;
}
.nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 120px;
  display: flex;
  z-index: 10;
}
.header__list-link {
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
}
.header {
  position: relative;
  z-index: 30;
}
</style>
