<template>
  <section class="traks">
    <div class="container">
      <div class="traks__row">
        <div class="traks__image">
          <img src="@/assets/img/elipse.png" alt="" />
        </div>
        <div class="traks__content content-traks">
          <div class="content-traks__heading heading">
            <div class="heading__equalizer">
              <img src="@/assets/img/music.png" alt="" />
            </div>
            <h2 class="heading__title">Last tracks</h2>
          </div>
          <div class="traks__controls controls">
            <div class="controls__play"></div>
            <div class="controls__progress"></div>
            <div class="controls__timer">00:00-00:00</div>
          </div>
          <audio class="traks__audio" ref="traksPlay" >
            <source src="../assets/audio/Hardwell.mp3" type="audio/mpeg" />
          </audio>
          <ol class="traks__list">
            <li class="traks__li" v-for="trak in traks" :key="trak.title">
              <p @click="playTraks" ref="asd" class="traks__trak">{{trak.title}}</p>
            </li>
          </ol>

          <div class="traks__subscribe">
            <div class="traks__text">Follow me:</div>
            <a href="" class="traks__link">
              <img src="@/assets/img/spoty.svg" alt="" />
              Spotify
            </a>
            <a href="" class="traks__link">
              <img src="@/assets/img/itunes.svg" alt="" />
              iTunes
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      audioSrc: "",
      audioTrak: true
    };
  },
  methods: {
    playTraks() {
     if(this.audioTrak){
      this.audioSrc = `/media/${this.$refs.asd.innerText}.bca607cc.mp3`;
      this.$refs.traksPlay.play()
      this.audioTrak = false
     }else{
      this.$refs.traksPlay.pause()
      this.audioTrak = true
     }
    },
  },

  props: ["traks"],
};
</script>

<style>

.traks {
  padding-bottom: 180px;
}
.traks__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  display: flex;
  align-items: center;
}
.traks__link:not(:last-child) {
  margin-right: 30px;
}
.traks__link img {
  margin-right: 10px;
}
.traks__text {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  margin-right: 40px;
  color: #ffffff;
}
.traks__subscribe {
  display: flex;
  align-items: center;
}
.traks__trak {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  transition: 0.3s;
  cursor: pointer;
}
.traks__trak:hover {
  color: #7a66cc;
}
.traks__list {
  margin-bottom: 50px;
  counter-increment: item;
}
.traks__li:nth-child(-n + 9)::before {
  content: "0" counter(item);
  counter-increment: item;
}
.traks__li::before {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  opacity: 0.5;
  margin-right: 16px;
}
.traks__li {
  display: flex;
  align-items: center;
}
.traks__li:not(:last-child) {
  margin-bottom: 20px;
}

.traks__audio {
  opacity: 0;
  visibility: hidden;
}
.traks__image {
  min-width: 510px;
  min-height: 510px;
  position: relative;
  margin-right: 60px;
  border: 18px solid rgba(122, 102, 204, 0.3);
  border-radius: 50%;
  padding: 16px;
}

.traks__image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.traks__image::before {
  content: "";
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 18px solid #7a66cc;
  animation: music linear 5s infinite both;
}
@keyframes music {
  0% {
    border-color: #5844a9;
  }

  100% {
    border-color: #47319e;
  }
}
.traks__controls {
  display: flex;
  align-items: center;
  margin-block: 50px;
}
.traks__row {
  display: flex;
  align-items: center;
}
</style>