        :pagination="{ clickable: true }"
        :loop="true"
        :breakpoints="swiperOptions.breakpoints"
      >
        >
        <swiper-slide class="slider__slide">
          <img src="@/assets/img/chandler.png" alt="" />
          <div class="slide-slider__content">
            <p class="slide-slider__text">Chandler, AZ</p>
            <a href="" class="slide-slider__title"
              >The Park at Wild Horse Pass</a
            >
            <div class="slide-slider__body">
              <p class="slide-slider__date">Apr 02 2021</p>
              <a href="" class="slide-slider__button">tickets</a>
            </div>
          </div>
        </swiper-slide>
        <swiper-slide class="slider__slide">
          <img src="@/assets/img/ibiza.png" alt="" />
          <div class="slide-slider__content">
            <p class="slide-slider__text">Ibiza, IBZ</p>
            <a href="" class="slide-slider__title">Swag Ibiza Club</a>
            <div class="slide-slider__body">
              <p class="slide-slider__date">Jul 08 2021</p>
              <a href="" class="slide-slider__button">tickets</a>
            </div>
          </div>
        </swiper-slide>
        <swiper-slide class="slider__slide">
          <img src="@/assets/img/ibiza-2.png" alt="" />
          <div class="slide-slider__content">
            <p class="slide-slider__text">Chandler, AZ</p>
            <a href="" class="slide-slider__title">El Swing Ibiza</a>
            <div class="slide-slider__body">
              <p class="slide-slider__date">Jul 10 2021</p>
              <a href="" class="slide-slider__button">tickets</a>
            </div>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </section>
</template>

<script>
import "swiper/css";
import "swiper/css/bundle";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation, Pagination, Scrollbar, A11y } from "swiper";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiperOptions: {
        breakpoints: {
          0: {
            slidesPerView: 1.1,
            spaceBetween: 15,
          },
          480: {
            slidesPerView: "auto",
            navigation: false,
          },
          767: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          991: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
        },
      },
    };
  },
};
</script>

