<template>
  <section class="first">
    <video
      poster="@/assets/img/header-bg.png"
      class="first__video"
      ref="video"
      @timeupdate="onTimeUpdateListener"
    >
      <source src="@/assets/video/panda.mp4" />
    </video>
    <div class="first__content">
      <div class="container first__content-item">
        <h2 class="first__title">New Single</h2>
        <h1 class="second__title">War For Love</h1>
        <div class="first__controls">
          <div
            class="controls__play"
            @click="playPause"
            @time="time"
            :class="click ? play : pause"
          ></div>
          <div class="controls__progress">
            <input
              @change="onTimeUpdateListenerMove"
              ref="input"
              type="range"
              :value="value"
              min="0"
              max="100"
            />
          </div>
          <div class="controls__timer">{{ time }}</div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      autoplay: true,
      value: 0,
      click: true,
      play: "controls__play",
      pause: "controls__play _pause",
      time: "00:00",
    };
  },
  methods: {
    onTimeUpdateListener() {
      this.value = Math.round(
        (this.$refs.video.currentTime / this.$refs.video.duration) * 100
      );
    },
    onTimeUpdateListenerMove() {
      this.$refs.video.currentTime =
        (this.$refs.input.value * this.$refs.video.duration) / 100;
      // console.log(this.$refs.input.value * this.$refs.video.duration / 100);
    },
    timeDecoder(seconds) {
      let sec = Math.floor(seconds % 60),
        hou = Math.floor(seconds / 60 / 60),
        min = Math.floor(seconds / 60) - hou * 60;

      min = min < 10 ? `0${min}` : min;
      sec = sec < 10 ? `0${sec}` : sec;

      const hms = hou > 0 ? `${hou}:${min}:${sec}` : `${min}:${sec}`;

      return hms;
    },
    playPause() {
      if (this.autoplay) {
        this.$refs.video.play();
        this.time = this.timeDecoder(this.$refs.video.duration);
      } else {
        this.$refs.video.pause();
      }

      this.click = !this.click;
      this.autoplay = !this.autoplay;
    },
  },
};
</script>

<style>
.first {
  position: relative;
  min-height: 100vh;
  display: flex;
}
.first__video {
  width: 100%;
  height: 100%;
  vertical-align: middle;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}
.first__content {
  width: 100%;
  flex: 1 1 auto;
  display: flex;
  align-items: flex-end;
  padding-top: 120px;
  padding-bottom: 280px;
}
.first__content-item {
  position: relative;
  z-index: 20;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.first__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #ffffff;
  margin-bottom: 10px;
}
.second__title {
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px #fff;
  margin-bottom: 50px;
}
.first__controls {
  display: flex;
  align-items: center;
}
.controls__progress {
  cursor: pointer;
  flex: 1 1 auto;
  height: 2px;
  display: flex;
  background-color: #fff;
}
.controls__progress {
  margin-right: 45px;
}
.controls__progress input {
  cursor: pointer;
  width: 100%;
  -webkit-appearance: none;
  background: #ffffff;
  background-repeat: no-repeat;
  cursor: pointer;
  height: 2px;
  background: #fff;
  background-image: linear-gradient(#7a66cc, #7a66cc);
  background-size: 0% 100%;
  background-repeat: no-repeat;
}
.controls__play {
  cursor: pointer;
  width: 22px;
  height: 25px;
  border-style: solid;
  border-width: 12.5px 0px 12.5px 22px;
  border-color: transparent transparent transparent #fff;
  margin-right: 45px;
}
.controls__play._pause {
  border-style: double;
  border-width: 0px 0 0px 15px;
}
.controls__timer {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
}
.first::before {
  pointer-events: none;
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 58%,
    #1c1b21 92.19%
  );
}
</style>